import * as React from 'react';
import { COLORS } from '@src/configs';

import { Col, HomeContainer, Row } from './home.styled';
import { useAppContext } from '../../context/app.context';

type IHomeProps = {};
const Home = (_: IHomeProps) => {
  const [state] = useAppContext();
  const [selected, setSelected] = React.useState({});
  const onTicketSelect = (i, j) => {
    if (selected[`${i}_${j}`] === 1) {
      setSelected({ ...setSelected, [`${i}_${j}`]: 0 });
    } else {
      setSelected({ ...setSelected, [`${i}_${j}`]: 1 });
    }
  };
  return (
    <HomeContainer>
      {state.tickets.map((item: any) => {
        return (
          <Row>
            {item.map((_item: any) => {
              return <Col onClick={() => {}} color={COLORS[_item.type]} />;
            })}
          </Row>
        );
      })}
    </HomeContainer>
  );
};
export default Home;
